*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    height: 100vh;
    /* font-family: 'Poppins'; */
    background-color: #4AA6FB;;
    
    font-family: 'Noto Sans';
    /* font-family: 'Comic Sans MS', cursive; */
}

.webgl,.webglcss
{
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}
.mainBarContainer{
    position:relative;
    /* width: 70%; */
}
.mainBar{
    z-index: 10;
    /*  position: absolute;  */
    position:absolute;
    width: 15vw;
}
.pannel
{
   
    background-color: black;  
    color: white; 
    border-radius: 1vw;
    padding: 1%;
    /* width: 200px; */
    /* height: 150px; */
    margin: 0.1vw;
    /* width: 20%; */
    font-size: 1vw;
}

.pannel > * span{
    vertical-align: super;
}

.mainBarContainer > * {
    user-select: none; /* supported by Chrome and Opera */
   -webkit-user-select: none; /* Safari */
   -khtml-user-select: none; /* Konqueror HTML */
   -moz-user-select: none; /* Firefox */
   -ms-user-select: none; /* Internet Explorer/Edge */
}

label.validationError{
    color: red;
}

/* .pannel>.title
{
    width: 100%;
} */

.pannel>button
{
    font-family: inherit;   
   color: white;
    background-color: blue; 
    /* border-radius: 3px; */
    border: none;
	border-radius: 0.3125rem; 
	/* box-shadow: 0 12px 24px 0 rgba(0,0,0,0.2); */
    padding: 1em;
    margin: 1em;
    width: 30%;
    height: 30%;
}

.pannel > div
{
    display: flex;
    align-items:center;
    width: 100%;
}

